import React, { Component } from 'react'
import {observer} from 'mobx-react'
import DocumentManagerContext from '../DocumentManager';
import { Checkbox, Divider, FormControlLabel, FormGroup, Radio, RadioGroup } from '@mui/material';
import PieceList from './PieceList';
import PieceEntry from './PieceEntry';

type Props = {}

type State = {}

class TelePage extends Component<Props, State> {
    static contextType = DocumentManagerContext;
    declare context: React.ContextType<typeof DocumentManagerContext>;
  state = {}
  
  constructor(props: Props) {
    super(props);

  }

  componentDidMount() {
  }

  componentWillUnmount(): void {
  }

  render() {    
    return (
      <>
      <div style={{display: (this.context.data.page == 2) ? "block": "none"}}>
            {/* <FormGroup row={true} style={{display:"flex", flexDirection:"row", justifyContent:"space-evenly", flexWrap:"nowrap"}}>
            <FormControlLabel
                labelPlacement='bottom'
                control={<Checkbox checked={this.context.data.playedDefense}/>} label="Played Defense" 
                
                value={this.context.data.playedDefense} onChange={(e, val)=>{this.context.data.setPlayedDefense(val)}}/>
            
            </FormGroup> */}
            <Divider>Climb</Divider>
            <FormGroup row={true} style={{display:"flex", flexDirection:"row", justifyContent:"space-evenly", flexWrap:"nowrap"}}>
              <RadioGroup
                row
                value={this.context.data.teleClimb}
                onChange={(e, val)=>{this.context.data.setTeleClimb(Number.parseInt(e.target.value) || 0)}}
                style={{margin:"auto"}}
              >
                <FormControlLabel value={-1} control={<Radio />} label="Fail" labelPlacement="bottom"/>
                <FormControlLabel value={0} control={<Radio />} label="No Try" labelPlacement="bottom"/>
                <FormControlLabel value={1} control={<Radio />} label="Center" labelPlacement="bottom"/>
                <FormControlLabel value={2} control={<Radio />} label="Edge" labelPlacement="bottom"/>

              </RadioGroup>
              </FormGroup>
              <Divider>Spotlight</Divider>
            <FormGroup row={true} style={{display:"flex", flexDirection:"row", justifyContent:"space-evenly", flexWrap:"nowrap"}}>
              <RadioGroup
                row
                value={this.context.data.spotlight}
                onChange={(e, val)=>{this.context.data.setSpotlight(Number.parseInt(e.target.value) || 0)}}
                style={{margin:"auto"}}
              >
                <FormControlLabel value={-1} control={<Radio />} label="N/A" labelPlacement="bottom"/>
                <FormControlLabel value={0} control={<Radio />} label="0/3" labelPlacement="bottom"/>
                <FormControlLabel value={1} control={<Radio />} label="1/3" labelPlacement="bottom"/>
                <FormControlLabel value={2} control={<Radio />} label="2/3" labelPlacement="bottom"/>
                <FormControlLabel value={3} control={<Radio />} label="3/3" labelPlacement="bottom"/>

              </RadioGroup>
              </FormGroup>
              <Divider>Trap</Divider>
              <PieceEntry piece={this.context.data.trap} color="blue" undoSide='left'></PieceEntry>
            </div>        
      </>
    )
  }
}

export default observer(TelePage)