import React from 'react';
import logo from './logo.svg';
import './App.css';
import QRPage from './components/QRPage';
import { DocumentManager } from './DocumentManager';
import AutoPage from './components/AutoPage';
import Footer from './components/Footer';
import ThemeProvider from '@mui/material/styles/ThemeProvider';
import createTheme from '@mui/material/styles/createTheme';
import TelePage from './components/ClimbPage';
import DrivingPage from './components/DrivingPage';
import TeamPage from './components/TeamPage';
import { TextField } from '@mui/material';
import { observer } from 'mobx-react';
import NotesPage from './components/NotesPage';



function App() {
  const documentManager = new DocumentManager();
  const DocumentManagerContext = React.createContext<DocumentManager>(documentManager);
  

  const darkTheme = createTheme({
    palette: {
      mode: 'dark',
    },
  });
  return (
    <ThemeProvider theme={darkTheme}>
    <DocumentManagerContext.Provider value={documentManager}>
    <div className="App" style={{
                width: "100%",
                height: "auto",
                maxHeight: "100%",
                display:'flex',
                flexDirection:'column',
                position: 'fixed',
                top:0,
                background: 'var(--background)',
            }}>
      <Footer></Footer>
      <div style={{
        overflowY: "scroll",
        WebkitOverflowScrolling: "touch"
      }}>
      <div style={{marginTop:"20px"}}>
      <TeamPage></TeamPage>
      <AutoPage></AutoPage>
      <TelePage></TelePage>
      <DrivingPage></DrivingPage>
      <QRPage></QRPage>
      </div>
          <NotesPage></NotesPage>
          </div>
      {/*  */}
      
    </div>
    </DocumentManagerContext.Provider>
    </ThemeProvider>
  );
}

export default observer(App);
