import React, { Component } from 'react'
import {observer} from 'mobx-react'
import DocumentManagerContext, { INumberEntry } from '../DocumentManager';
import { Badge, ButtonGroup, IconButton } from '@mui/material';
import { Square, Clear, Add, Remove } from '@mui/icons-material';

type Props = {
    piece: INumberEntry;
    color: string;
    undoSide: "left" | "right";
}

type State = {}


class PieceList extends Component<Props, State> {
    static contextType = DocumentManagerContext;
    declare context: React.ContextType<typeof DocumentManagerContext>;
  state = {}

  componentDidMount() {
  }

  componentWillUnmount(): void {
  }

  render() {    
    return (
      <>
                <div style={{fontSize: "48px"}}>

                  {this.props.undoSide === "left" && <IconButton onClick={()=>this.props.piece.dec()}>
                    <Remove style={{fontSize: "200%"}}></Remove>
                  </IconButton>}
                  <IconButton onClick={()=>this.props.piece.inc()} size='large' style={{}}>
                  <Badge badgeContent={this.props.piece.value} color="secondary">
                    <Add style={{fontSize: "200%"}}></Add>
                    </Badge>
                  </IconButton>
                  {this.props.undoSide === "right" && <IconButton onClick={()=>this.props.piece.dec()}>
                    <Remove style={{fontSize: "200%"}}></Remove>
                  </IconButton>}
                </div>
      </>
    )
  }
}

export default observer(PieceList)