import React, { Component } from 'react'
import {observer} from 'mobx-react'
import DocumentManagerContext from '../DocumentManager';
import { Box, Checkbox, Divider, FormControl, FormControlLabel, FormGroup, InputLabel, MenuItem, Radio, RadioGroup, Select } from '@mui/material';
import PieceList from './PieceList';
import PieceEntry from './PieceEntry';
import TelePage from './ClimbPage';

type Props = {}

type State = {}

class AutoPage extends Component<Props, State> {
    static contextType = DocumentManagerContext;
    declare context: React.ContextType<typeof DocumentManagerContext>;
  state = {}
  
  constructor(props: Props) {
    super(props);

  }

  componentDidMount() {
  }

  componentWillUnmount(): void {
  }

  render() {    
    return (
      <>
        <div style={{display: (this.context.data.page == 1) ? "block": "none", marginBottom:"10px"}}>
            <div style={{display:"flex", flexDirection:"row", justifyContent:"space-around"}}>
              <div>
                SPEAKER
                <PieceEntry piece={this.context.data.autoSpeaker} color="blue" undoSide='right'></PieceEntry>
                SPEAKER MISS
                <PieceEntry piece={this.context.data.autoSpeakerMiss} color="blue" undoSide='right'></PieceEntry>
              </div>


              <div>
                AMP
                <PieceEntry piece={this.context.data.autoAmp} color="blue" undoSide='left'></PieceEntry>
                <FormControlLabel
                labelPlacement='top'
                control={<Checkbox checked={this.context.data.autoCrossed}/>} label="Cross Line"
                value={this.context.data.autoCrossed} onChange={(e, val)=>{this.context.data.setAutoCross(val)}} />
              </div>
              
            </div>
            <div>
            <FormGroup row={true} style={{display:"flex", flexDirection:"row", justifyContent:"space-evenly", flexWrap:"nowrap"}}>

            </FormGroup>
            <Divider>TELEOP</Divider>
            
            <div style={{display:"flex", flexDirection:"row", justifyContent:"space-around"}}>
              <div>
                SPEAKER
              <PieceEntry piece={this.context.data.teleSpeaker} color="blue" undoSide='right'></PieceEntry>
              SPEAKER MISS
                <PieceEntry piece={this.context.data.teleSpeakerMiss} color="blue" undoSide='right'></PieceEntry>

              </div>
              <div>
                AMP
              <PieceEntry piece={this.context.data.teleAmp} color="blue" undoSide='left'></PieceEntry>
              <FormControlLabel
                labelPlacement='top'
                control={<Checkbox checked={this.context.data.playedDefense}/>} label="Played Defense" 
                
                value={this.context.data.playedDefense} onChange={(e, val)=>{this.context.data.setPlayedDefense(val)}}/>
              </div>
            </div>
            </div>  
            </div>      
      </>
    )
  }
}

export default observer(AutoPage)